import Select from './select/select'
import Dialog from './dialog/dialog'
import Toast from './toast/toast'
import Message from './message/message'
import Button from './button/button'
import Tree from './tree/tree'
import Card from './card/card'
import Page from './page/page'
import Form from './form/form'
import Radio from './radio/radio'
import Checkbox from './checkbox/checkbox'
import Switch from './switch/switch'
import Tooltip from './tooltip/tooltip'

export default [
  { path: 'select', component: Select, meta: {title: 'Select '} },
  { path: 'dialog', component: Dialog, meta: {title: 'Dialog '} },
  { path: 'toast', component: Toast, meta: {title: 'Toast '} },
  { path: 'message', component: Message, meta: {title: 'Message '} },
  { path: 'button', component: Button, meta: {title: 'Button '} },
  { path: 'tree', component: Tree, meta: {title: 'Tree '} },
  { path: 'card', component: Card, meta: {title: 'Card '} },
  { path: 'pages', component: Page, meta: {title: 'Page '} },
  { path: 'form', component: Form, meta: {title: 'Form '} },
  { path: 'radio', component: Radio, meta: {title: 'Radio '} },
  { path: 'checkbox', component: Checkbox, meta: {title: 'Checkbox '} },
  { path: 'switch', component: Switch, meta: {title: 'Switch '} },
  { path: 'tooltip', component: Tooltip, meta: {title: 'Tooltip '} },
  { path: '', redirect: 'button'}
]



