<template>
    <div class="section">
        <div class="main">
            <h2 class="section-label">Tooltip </h2>
            <h4></h4>
            <div class="demo-block">
                <v-tooltip class="item" effect="dark" content="top " placement="top" trigger="hover">
                    <v-button size="small"></v-button>
                </v-tooltip>
                 <v-tooltip class="item" effect="dark" content="bottom " placement="bottom" trigger="hover">
                    <v-button size="small"></v-button>
                </v-tooltip>
                 <v-tooltip class="item" effect="dark" content="left " placement="left" trigger="hover">
                    <v-button size="small"></v-button>
                </v-tooltip>
                 <v-tooltip class="item" effect="dark" content="right " placement="right" trigger="hover">
                    <v-button size="small"></v-button>
                </v-tooltip>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-tooltip  class="item" effect="dark"  content="tooltip" placement="top" trigger="hover">
                  <v-button size="small">Dark</v-button>
                </v-tooltip >
              <v-tooltip  class="item" effect="light"  content="tooltip"  placement="top" trigger="hover">
                  <v-button size="small">Lignt</v-button>
                </v-tooltip >
            </div>
            <h4>content</h4>
            <div class="demo-block">
                <v-tooltip  class="item" effect="dark"  placement="top" trigger="hover">
                  <v-button size="small">Dark</v-button>
                   <p slot="content"><br/></p>
                </v-tooltip >
            </div>
        </div>
    </div>
</template>

<script>
    import VTooltip from "../../common/tooltip/tooltip";
    import VButton from "../../common/button";
    
    export default {
        components: {
            VTooltip,
            VButton
        },
        data() {
            return {
                value: true,
                value2:false
            }
        },
        methods: {
            onFormChange(v) {
                this.value = v;
            },
        }
    
    }
</script>

<style lang="scss" scoped>
    
</style>




